body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

::-webkit-scrollbar {
  display: none;
}

@font-face {
  font-family: "blonde";
  src: url("./assets/fonts/blonde.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "boytoy";
  src: url("./assets/fonts/boytoyitalic-regular_1-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
